@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

.bookGrid__wrap {
  width: 85%;
}

.bookGrid__heading {
  color: var(--Black-60, #45464E);
  font-family: 'Inter';
  font-weight: 500;
  font-size: 1.2vw;
  margin-bottom: 2.5vw;
}

.bookGrid__grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0.4vw;

}

.bookGrid__grid-item {
  width: 10.4vw;
  height: 17vw;
  margin-bottom: 7vw;
}

.bookGrid__grid-item img {
    width: 100%;
    height: 17vw;
    border-radius: 1vw;
    object-fit: cover;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    cursor: pointer;
  }
  
  .bookGrid__details {
    margin:0.3vw 0.2vw;
    font-family: Inter;
  }
  
  .bookGrid__name {
    font-size: 1.1vw;
    font-weight: 700;
    color: #333;
    color: var(--Black-60, #45464E);
  }
  
  .bookGrid__author {
    font-size: 1vw;
    color: #666;
    color: var(--Black-60, #45464E);
    font-weight: 500;
  }
  
  .bookGrid__bookmarks {
    font-size: 0.9vw;
    color: #45464E;
    color: var(--Black-60, #45464E);
    font-weight: 500;
    margin-top: 0.6vw;
  }
  .carousel__skeleton-loader {
    display: flex;
  }
  
  .carousel__skeleton-item {
    flex-shrink: 0;
    width: 10.4vw;
    height: 17vw;
    margin: 0 0.4vw;
    border-radius: 1vw;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
    background-size: 200% 100%;
    animation: loading 1.5s infinite linear;
  }
  
  @keyframes loading {
    0% {
      background-position: 200% 0;
    }
    100% {
      background-position: -200% 0;
    }
  }